

// Text style
//
// Typographic settings for different text types.
// Each text type has set a `font`, a `scale`, a `color` and parameters like letter-spacing, max-width or else.
// Everything text uses this `text-style` mixin. We don't set `color` or `font` directly for a text.
//
// Styleguide text-style

// The width of a text block

$text-block-width: 25em; 



// The text mixin
@mixin text-style($variant) {
  
  // Default
  // - The basic settings for a text.
  @if ($variant == 'default') {
    @include font(nimbus-sans-regular);
    @include scale(default);
    @include colors(black-on-white);
    
    letter-spacing: 1.1px;
    
    
  }
  
  // Body text
  // - Settings for long text blocks.
  @if ($variant == 'body-text') {
    @include p;
    
    max-width: 25em;
    
    
  }
  
  // Title
  // - Section titles
  @if ($variant == 'title') {
    @include font(nimbus-sans-black);
    
    
    
  }
  
  // Large text
  // - Large text block
  @if ($variant == 'large-text') {
    @include scale(large-3);
    
    
    
    @include portrait {
      @include scale(large-2);
      
      
    }
    
  }
  
}


// Text style classes

.text-style--default {
  @include font(nimbus-sans-regular);
  @include scale(default);
  @include colors(black-on-white);
  
  letter-spacing: 1.1px;
  
  
}

.text-style--body-text {
  @include p;
  
  max-width: 25em;
  
  
}

.text-style--title {
  @include font(nimbus-sans-black);
  
  
  
}

.text-style--large-text {
  @include scale(large-3);
  
  
  
  @include portrait {
    @include scale(large-2);
    
    
  }
  
}

